<template>
  <div>
    <van-nav-bar :title="$route.query.text" left-arrow @click-left="revert" />

    <van-row class="box-row">
      <van-col class="col" span="6" @click="recommend">推荐</van-col>
      <van-col class="col" span="6" @click="priceRanking">价格</van-col>
      <van-col class="col" span="6" @click="salesVolume">销量</van-col>
      <van-col class="col" span="6" @click="upNew">上新</van-col>
    </van-row>

    <van-row>
      <van-col
        span="12"
        v-for="(item,index) in list"
        :key="index"
        @click="$router.push({path:'/details',query:{ index: index}})"
      >
        <!-- 图片 -->
        <div class="box">
          <img :src="item.url" alt />
        </div>
        <!-- 价格 -->
        <div class="price">
          <span class="present-price">{{item.presentPrice}}</span>
          <span class="original-price">{{item.originalPrice}}</span>
        </div>
        <!-- 描述 -->
        <p class="describe">{{item.describe}}</p>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      boolprice: false,
      boolsales: false
    };
  },
  methods: {
    // 排序方法
    sortFun(attr, rev) {
      if (rev == undefined) {
        rev = 1;
      } else {
        rev = rev ? 1 : -1;
      }
      return function(a, b) {
        a = a[attr];
        b = b[attr];
        if (a < b) {
          return rev * -1;
        }
        if (a > b) {
          return rev * 1;
        }
        return 0;
      };
    },
    revert() {
      this.$store.state.bool = true;
      this.$router.push('/home');
    },
    recommend() {
      this.list = JSON.parse(JSON.stringify(this.$store.state.classify.Single));
    },
    priceRanking() {
      this.boolprice = !this.boolprice;
      this.list = this.list.sort(this.sortFun(`presentPrice`, this.boolprice));
    },
    salesVolume() {
      this.boolsales = !this.boolsales;
      this.list = this.list.sort(this.sortFun(`SalesVolume`, this.boolsales));
    },
    upNew() {
      this.list = this.list.sort(this.sortFun(`new`, true));
    }
  },
  created() {
    this.$store.state.bool = false;
    this.list = JSON.parse(JSON.stringify(this.$store.state.classify.Single));
  }
};
</script>

<style lang="scss" scoped>
.box-row {
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 5px;
  .col {
    font-size: 14px;
    color: #666;
  }
}
</style>